<template>
  <div class="search-box">
    <div class="search-area" :class="{'search-area-focus': isFocus}">
      <div class="search-icon"><img :src="icon" alt=""></div>
      <div class="search-input">
        <span v-if="isPlaceholder">{{placeholder}}</span>
        <input v-else type="text" id="xSearchInput" ref="search"
          v-model="innerValue"
          :placeholder="placeholder"
          :autofocus="focus"
          @focus="onFocus"
          @blur="onBlur"
          @keyup.enter="onSearch"
        />
      </div>
      <div v-show="hasInput" @click="onClear" class="search-clear"></div>
    </div>
    <button v-show="hasInput" @click="onSearch" class="search-btn">搜索</button>
  </div>
</template>

<script>
export default {
  name: 'SearchBox',
  model: {
    prop: 'value',
    event: 'input'
  },
  props: {
    value: {
      type: String,
      default: null
    },
    isPlaceholder: {
      type: Boolean,
      default: () => false
    },
    placeholder: {
      type: String,
      default: '输入关键词搜索商品'
    },
    focus: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      icon: 'data:image/png;base64,',
      isFocus: false,
      hasInput: false,
      innerValue: ''
    }
  },
  watch: {
    innerValue (val) {
      if (val && !this.hasInput) {
        this.hasInput = true
      } else if (!val && this.hasInput) {
        this.hasInput = false
      }
      // 如果调用此组件使用了 v-model，则同步它
      if (val !== this.value) this.$emit('input', val)
    },
    value (val) {
      this.innerValue = val
    },
    focus (val) {
      if (val) this.setFocus()
    }
  },
  mounted () {
    if (this.value !== null) this.innerValue = this.value
  },
  methods: {
    onFocus () {
      this.isFocus = true
    },
    onBlur () {
      this.isFocus = false
    },
    onSearch () {
      this.$emit('search', this.innerValue)
      console.log(this.innerValue)
    },
    onClear () {
      this.innerValue = ''
      this.setFocus()
    },
    setFocus () {
      if (this.$refs.search) this.$refs.search.focus()
    }
  }
}
</script>

