  position: relative;
  height: 100%;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 17px;
  /* border: 1px solid #eee; */
  box-shadow: 0 0 7px #ddd;
  background: white;
