}
.search-icon img {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
}
.search-input {
  position: relative;
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  color: #666;
  padding-left: 6px;
  padding-right: 35px;
  box-sizing: border-box;
}
.search-input input {
  width: 100%;
